import { Fragment, Suspense, lazy, useContext } from 'react';
import './App.scss';
import Navbar from './components/Navbar';
import AppContext from './store/AppContext';
import { Routes, Route, } from 'react-router-dom';
import Progress from './utils/Progress';
import {useLocation} from "react-router-dom";


const Landing = lazy(() => import('./pages/Landing')),
      Login = lazy(() => import('./pages/Login')),
      SignUp = lazy(() => import('./pages/SignUp')),
      Jobs = lazy(() => import('./pages/Jobs')),
      Companies = lazy(() => import('./pages/Companies')),
      CompanyPage = lazy(() => import('./pages/CompanyPage')),
      Profile = lazy(() => import('./pages/Profile')),
      basePath = "https://huguesbert17.github.io/jobly"


function App() {
  const {user} = useContext(AppContext),
    { hash } = useLocation()

  if(user?.isLogged && ["#/login", "#/signup"].includes(window.location.hash)) window.location.href = basePath

  if(!user?.isLogged && ["#/jobs", "#/companies", "#/profile"].includes(window.location.hash)) window.location.href = basePath

  console.log(!user?.isLogged, hash, ["#/jobs", "#/companies", "#/profile"].includes(window.location.hash));

  return <Fragment>
    <Navbar/>
    <div className='container' style={{paddingTop: "40px", paddingBottom: "40px"}}>
      <Suspense fallback={<Progress style="skype"/>}>
        <Routes>
          {(!user?.isLogged) && <Route path='/' element={<Landing/>}/>}
            {(!user?.isLogged) && <Route path='login' element={<Login/>}/>}
            {(!user?.isLogged) && <Route path='signup' element={<SignUp/>}/>}

            <Route path='/' element={<Jobs/>}/>
            <Route path='/jobs' element={<Jobs/>}/>
            <Route path='/profile' element={<Profile/>}/>
            <Route path='/companies'>
              <Route path='' element={<Companies/>}/>
              <Route path=':company' element={<CompanyPage/>}/>
            </Route>
        </Routes>
      </Suspense>
    </div>
  </Fragment>;
}

export default App;
